      ];

      const onSetTranslate = (el) => {
         if (el.isEnd) {
            el.$wrapperEl[0].style.transform = `translate3d(${
               el.translate + (el.translate - el.previousTranslate)
            }px, 0px, 0px)`;
         }
      };

      const breakpoints = {
         768: {
            slidesPerView: 3.5,
         },
         524: {
            slidesPerView: 2.5,
         },
         200: {
            slidesPerView: 1.5,
         },
      };

      return {
         slides,
